<template>
  <div class="mapTable" >
    <div class="modelBox">
      <p>
        <strong :class="{ active : tabState === 0 }" @click="tabState = 0">
          采集信息
        </strong>
        <strong :class="{ active : tabState === 1 }" @click="tabState = 1">
          采集位置
        </strong>
      </p>
      <div class="box">
        <div class="tab_box" v-show="tabState === 0">
          <DataTableTabInfo :infoData="infoData"></DataTableTabInfo>
        </div>
        <div class="tab_box" v-show="tabState === 1">
          <DataTableMap ref="map"></DataTableMap>
      </div>
      </div>
    </div>
  </div>
</template>

<script>
import DataTableTabInfo from './DataTable_tab_info'
import DataTableMap from './DataTable_map'
import { getTableInfo } from '@/api/dataCollection'
export default {
  name: 'MapTable',
  data () {
    return {
      /* tab选项卡状态 */
      tabState: 0,
      infoData: {
        filed_name: []
      },
      lon: '',
      lat: ''
    }
  },
  props: {
    pkid: {}
  },
  components: {
    DataTableTabInfo,
    DataTableMap
  },
  watch: {
    pkid () {
      this.getTableInfo()
    }
  },
  created () {
  },
  mounted () {
  },
  methods: {
    /* 获取字段列表 */
    getTableInfo () {
      getTableInfo({
        'data': {
          id: this.pkid * 1
        }
      }).then(res => {
        if (res.data.code === '0000') {
          /* 将数据传给采集信息接口 */
          this.infoData = res.data.data
          if (this.infoData === null) {
            this.infoData = {
              filed_name: []
            }
          }
          this.lon = this.infoData.survey_lon
          this.lat = this.infoData.survey_lat
          /* 将数据传给采集位置地图 */
          this.$refs.map.addPointToMap([{ longitude: this.lon, latitude: this.lat }], [226, 119, 40], 'inflectionPoint')
          this.$refs.map.ondbclickDingwei(this.infoData.survey_lon, this.infoData.survey_lat)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped rel="stylesheet/scss">
  .mapTable {
    height: 100%;
    .btnBox {
      position: absolute;
      right: 10px;
      top: 10px;
    }
    .modelBox {
      background: #F5F5F5;
      height: 100%;
      p {
        padding: 0 15px 0 0;
        position: relative;
        overflow: hidden;
        text-align: left;
        strong {
          float: left;
          display: inline-block;
          cursor: pointer;
          height: 100%;
          font-size: 14px;
          padding: 5px 20px;
        }
        .active {
          border: 1px solid #DADCE0;
          background: #fff;
          border-bottom: 0;
          box-shadow: 0 0 7px 3px rgba(0,0,0,0.1);
        }
        span {
          float: right;
          cursor: pointer;
        }
      }
      .box {
        background: #fff;
        overflow: hidden;
        padding: 0 0 0 5px;
        height: calc(100% - 32px);
        .tab_box{
          width: 100%;
          height: 100%;
          z-index: 13131313;
          border-top:1px solid #DADCE0;
          overflow: auto;
        }
      }
    }
  }
</style>
