<template>
  <div id="tablet">
    <QueryBox @searchSrldId="searchSrldId"></QueryBox>
    <div>
        <div>
          <Table :columns="columns1" :data="data1" highlight-row></Table>
          <div class="page_box">
            <Page :total="total" :page-size="pageSize" @on-change="pageChange" show-total/>
          </div>
        </div>
        <div class="tab_box" ref="drawerBox" :class="{active:drawer}">
          <DataTableTab ref="tab" :pkid="pkid"></DataTableTab>
          <Button type="primary" size="small" style="position: absolute;bottom: 20px;right:20px;" @click="drawer=false">返回</Button>
        </div>
    </div>
  </div>
</template>

<script>
import QueryBox from './DataTable_query'
import DataTableTab from './DataTable_tab'
import { getSurveyTableList } from '@/api/dataCollection'
export default {
  name: 'ActualTablet',
  data () {
    return {
      pointData: [],
      spotTable: false,
      split1: 0.6,
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '红线编码',
          key: 'srld_code'
        },
        {
          title: '调查表类型',
          key: 'index_item'
        },
        {
          title: '采集时间',
          key: 'survey_time'
        },
        {
          title: '操作',
          key: 'address',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.pkid = params.row.pk_id
                    this.drawer = true
                    this.$refs.tab.tabState = 0
                  }
                }
              }, '查看')
            ])
          }
        }
      ],
      data1: [],
      /* 传递给采集信息组件 */
      pkid: '',
      /* 查询框查询条件 */
      obj: {},
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((window.innerHeight - 343) / 48),
      /* 抽屉 */
      drawer: false
    }
  },
  components: {
    QueryBox,
    DataTableTab
  },
  created () {
    this.getSurveyTableList()
  },
  mounted () {
    // 窗口改变时图表自适应
    window.addEventListener('resize', () => {
      this.pageSize = Math.floor((window.innerHeight - 350) / 48) + 1
      this.getSurveyTableList()
    }, false)
  },
  watch: {
    obj () {
      this.getSurveyTableList()
    }
  },
  methods: {
    /* 接受查询框查询条件 */
    searchSrldId (obj) {
      this.obj = obj
    },
    /* 获取字段列表 */
    getSurveyTableList () {
      getSurveyTableList({
        'data': {
          pageSize: this.pageSize,
          pageNum: this.pageNum,
          param: this.obj.param,
          redlineName: this.obj.srldName,
          tableName: this.obj.param
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
        }
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getSurveyTableList()
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .map_box{
      position:absolute;
      width:60%;
      top: 55px;
      bottom: 0;
    }
    .demo-split{
      height: calc(100% - 54px);
    }
    .demo-split-pane{
      height: 100%;
    }
    .page_box{
      position: absolute;
      bottom: 0;
      right: 0;
      width: 100%;
      padding: 15px;
      text-align: right
    }
    .tab_box{
      width: 700px;
      border: 1px solid #d1d1d1;
      position: absolute;
      right: -100%;
      top:55px;
      height: calc(100% - 53px);
      z-index: 100;
      transition: all 0.8s ease;
    }
    .active{
      right: -1px
    }
  }
</style>
