<template>
  <div class="dataTableTabInfo" >
    <h5>基础数据</h5>
    <Row class="rowtTable">
      <Col span="12">
        <Row>
          <Col span="12" class="colKey">经度</Col>
          <Col span="12" class="colValue" v-if="infoData.survey_lon === ''" style="text-align: center">—</Col>
          <Col span="12" class="colValue" v-if="infoData.survey_lon !== ''">{{cacuLonLat(infoData.survey_lon)}}</Col>
        </Row>
      </Col>
      <Col span="12">
        <Row>
          <Col span="12" class="colKey">纬度</Col>
          <Col span="12" class="colValue" v-if="infoData.survey_lat === ''" style="text-align: center">—</Col>
          <Col span="12" class="colValue" v-if="infoData.survey_lat !== ''">{{cacuLonLat(infoData.survey_lat)}}</Col>
        </Row>
      </Col>
      <Col span="12">
        <Row>
          <Col span="12" class="colKey">海拔</Col>
          <Col span="12" class="colValue" v-if="infoData.survey_altitude === ''" style="text-align: center">—</Col>
          <Col span="12" class="colValue" v-if="infoData.survey_altitude !== ''">{{infoData.survey_altitude}}</Col>
        </Row>
      </Col>
      <Col span="12">
        <Row>
          <Col span="12" class="colKey">温度</Col>
          <Col span="12" class="colValue" v-if="infoData.survey_temperature === ''" style="text-align: center">—</Col>
          <Col span="12" class="colValue" v-if="infoData.survey_temperature !== ''">{{infoData.survey_temperature}}</Col>
        </Row>
      </Col>
      <!--<Col span="12">
        <Row>
          <Col span="12" class="colKey">天气</Col>
          <Col span="12" class="colValue" v-if="infoData.survey_weather === ''" style="text-align: center">—</Col>
          <Col span="12" class="colValue" v-if="infoData.survey_weather !== ''">{{infoData.survey_weather}}</Col>
        </Row>
      </Col>-->
    </Row>
    <br>
    <h5>调查数据</h5>
    <Row class="rowtTable" v-if="infoData.filed_name.length === 0">
      <Col span="24">
        <Row>
          <Col span="24" class="colKey" style="text-align: center;font-weight: normal">暂无调查数据......</Col>
        </Row>
      </Col>
    </Row>
    <Row class="rowtTable" v-if="infoData.filed_name.length === 1">
      <Col span="24">
        <Row>
          <Col span="6" class="colKey">{{infoData.filed_name[0].key}}</Col>
          <Col span="18" class="colValue" v-if="infoData.filed_name[0].value === ''" style="text-align: center">—</Col>
          <Col span="18" class="colValue" v-if="infoData.filed_name[0].value !== ''">{{infoData.filed_name[0].value}}</Col>
        </Row>
      </Col>
    </Row>
    <Row class="rowtTable" v-if="infoData.filed_name.length > 1">
      <Col span="12" v-for="(item, index) in infoData.filed_name" :key="index">
        <Row>
          <Col span="12" class="colKey">{{item.key}}</Col>
          <Col span="12" class="colValue" v-if="item.value === ''" style="text-align: center">—</Col>
          <Col span="12" class="colValue" v-if="item.value !== ''">{{item.value}}</Col>
        </Row>
      </Col>
    </Row>
  </div>
</template>

<script>
import { cacuLonLat } from '@/utils/common'
export default {
  name: 'DataTableTabInfo',
  data () {
    return {
    }
  },
  props: {
    infoData: {
      default: {
        filed_name: []
      }
    }
  },
  components: {
  },
  watch: {
  },
  created () {
  },
  mounted () {
  },
  methods: {
    cacuLonLat (num) {
      return cacuLonLat(num)
    }
  }
}
</script>

<style lang="scss" scoped rel="stylesheet/scss">
  .dataTableTabInfo{
    margin:8px;
    h5{
      margin:10px 0 5px;
    }
    .rowtTable{
      border-top:1px solid #d1d1d1;
      border-left:1px solid #d1d1d1;
    }
    .colKey,.colValue{
      padding:8px 10px;
      border-right: 1px solid #d1d1d1;
      border-bottom: 1px solid #d1d1d1;
      height:35px;
      overflow: hidden;
    }
    .colKey{
      text-align: center;
      font-weight: bolder;
    }
    .colValue{
      text-align: left;
    }
  }
</style>
